.goto{
    margin-bottom: 100px;
}
.block_goto{
    width: 500px;
    height: 200px;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgba(0, 0, 0, 0.2);
    margin: 0 auto;
    box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.1);

    .subtitle-medium{
        text-align: center;
        padding: 10px 0;
    }
}
.flex_goto{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 35px 50px;
}
.send_goto{
    background-color: #ff8f3e;
    color: #fff;
    font-weight: 700;
    font-size: 18px;
    display: flex;
    align-items: center;
    padding: 20px 30px;
    border-radius: 5px;
    cursor: pointer;

    transform: skewX(-7deg);

    &:hover{
        background-color: #fe964c;
    }
}
.img_goto{
    width: 25px;
    height: 25px;
    display: inline-block;
    margin-right: 15px;
}
.gif_goto{
    width: 80px;
    height: 80px;
}



@media (max-width: 600px){
    .block_goto{
        width: 300px;
    }
    .flex_goto{
        margin: 15px 20px;
    }
}