.skills_section{
    margin-bottom: 800px;

    .title-medium {
        margin-bottom: 25px;
    }
}
.skills_section_block{
    position: relative;

}
.name_blockSkill1{
    color: rgba(38, 76, 228, 0.14);
    font-size: 42px;
    font-weight: 700;
    margin-bottom: 5px;
    position: absolute;
    top: 160px;
    left: -110px;
    z-index: -1;
    transition: all 0.3s;
    transform: rotate(-90deg);
}
.skills{
    display: inline-block;
    width: 40%;
    min-width: 405px;
    padding: 10px;
    border-radius: 10px;
    background-color: #fff;

    -webkit-box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);

    &:hover .name_blockSkill1{
        left: -150px;
    }
}

.oneSkill{
    padding: 15px 5px;
    display: flex;
    align-items: center;

    &:hover{
        .longer_skill{
            height: 2px;
        }
    }
}
.point_skill{
    min-width: 7px;
    min-height: 7px;
    border: 2px solid grey;
    border-radius: 50%;
}
.logo_skill{
    width: 25px;
    height: 25px;
    min-width: 25px;
    min-height: 25px;
    fill: rgb(193, 193, 193);
    margin: 0 0 0 10px;

}
.js_skill{
    fill: #F0DB4F;
}
.name_skill{
    font-size: 22px;
    margin: 0 10px;
}
.longer_skill{
    width: 100%;
    background-color: grey;
    height: 1px;
    opacity: 0.5;
}
.success_skill{
    min-width: 30px;
    min-height: 30px;
    border: 2px solid grey;
    border-radius: 50%;
    margin: 0 10px;
    transition: all 0.3s;
}
.success_skill--active{
    border: 15px solid #8ed58e;
}
.success_skill--active--svg{
    background-color: #8ed58e;
    border: 2px solid #8ed58e;
    position: relative;

    &::after{
        content: "";
        display: inline-block;
        width: 25px;
        height: 25px;
        position: absolute;
        top: 5px;
        left: 3px;

        background-image: url('../img/svg/success.svg');
        background-repeat: no-repeat;
    }
}


// Табличка мои работы, не касается скилов
.banner_MyWorks{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;

    .title-medium{
        margin: 0 40px;
        background: linear-gradient(-45deg,#ffa63d,#ff3d77,#b933f9,#5f19dd);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}
.line_MyWorks{
    background: linear-gradient(-45deg,#ffa63d,#ff3d77,#b933f9,#5f19dd);
    width: 100%;
    height: 2px;
}

.line_left_MyWorks{
    background: linear-gradient(90deg,#ffa63d,#ff3d77,#b933f9,#5f19dd);

}
// ... Табличка мои работы, не касается скилов




// MEDIA media MEDIA media MEDIA media MEDIA media MEDIA media MEDIA media 
// media запросы

@media (max-width: 1200px){
    .skills_section_block{
        display: flex;
        flex-direction: column;
    }
    .skills_section{
        margin-bottom: 100px;
    }
}
@media (max-width: 430px){
    .skills{
        width: 100%;
        min-width: inherit;
    }
    .name_skill{
        font-size: 12px;
    }
    .name_blockSkill1{
        display: none;
    }
}