.back{
    position: absolute;
    left: 80px;
    bottom: 22px;
    width: 140px;
    height: 8px;
    background-color: rgb(77, 77, 77);
    border-radius: 10px;
    z-index: 100;
    box-shadow: 0px 18px 53px 3px rgba(34, 60, 80, 0.34);
}
.inPhone{

}
.phoneTime{
    position: absolute;
    top: 25px;
    left: 40px;

    font-size: 12px;
    color: #fff;
}
.phoneBattery{
    position: absolute;
    top: 25px;
    right: 40px;
    width: 15px;
    height: 15px;
}
.phoneTimeAll{
    position: absolute;
    top: 5px;
    left: 22px;

    font-size: 12px;
    color: black;
}
.phoneBatteryAll{
    position: absolute;
    top: 5px;
    right: 30px;
    width: 15px;
    height: 15px;
}
.listApps{
    position: absolute;
    top: 70px;
    left: 30px;
    right: 30px;
    display: flex;
    flex-wrap: wrap;
}
.blockWithOneApp{
    border-radius: 10px;
    margin-right: 13px;
    margin-bottom: 35px;
    transition: all 0.2s;
    max-width: 50px;
    
    .subtitle-maxsmall{
        text-align: center;
        color: #fff;
    }

    a {
        cursor: none;
    }

    &:hover{
       transform: scale(105%); 
    }
}
.iconForOneApp{
    width: 50px;
    height: 50px;
    border-radius: 15px;
}

// Нижняя менюшка
.menuFootApp{
    display: flex;
    position: absolute;
    left: 26px;
    bottom: 30px;
    background-color: rgba(255, 255, 255, 0.452);
    border-radius: 20px;
    height: 70px;
    align-items: center;
    padding-left: 5px;

    .blockWithOneApp{
        margin: 0;
        margin-right: 13px;
        
        &:last-child{
            margin-right: 5px;
        }
    }
}

// Уведомление 

#alertBlock {
    width: 220px;
    height: 30px;
    position: absolute;
    top: 45px;
    left: -250px;
    display: block;
    background-color: rgba(0, 123, 255, 0.338);
    border-radius: 15px;
    font-size: 14px;
    color: #fff;
    text-align: center;
    padding-top: 5px;
    transition: all 0.3s;
}
.alertBlock--active{
    left: 25px !important;
}

.dir_manyAppInOne{
    width: 50px;
    height: 50px;
    margin-bottom: 5px;
    background-color: rgba(108, 108, 108, 0.463);
    border-radius: 12px;

    display: flex;
    padding: 5px 5px;
}
