.footer{
    background-color: black;
    width: 100%;
    margin-top: 30px;
}
.block_footer{
    min-height: 100px;
    color: #fff;

    display: flex;
    justify-content: space-between;
}
.left_foot{
    display: flex;
    align-items: center;

}
.right_foot{
    padding: 15px;

    li{
        &:hover{
            text-decoration: underline;
        }
    }
}
