.calculatorPhone{
    width: 280px;
    height: 580px;
    top: 10px;
    left: 10px;
    position: absolute;
    background-color: #171717;
    border-radius: 33px;
    transition: all 0.4s;
    z-index: 100;
    
    .phoneTimeAll{
        color: #fff;
    }

    &:before{
        content: "";
        position: absolute;
        width: 154px;
        height: 50px;
        display: inline;
        top: 0;
        left: 56px;
        background-image: url(./../img/png/headerPhone.png);
        background-repeat: no-repeat;
    }
    
}


.calculatorBlock{
    display: flex;
    flex-direction: column;
    height: 100%;
}
.viewResult_calculator{
    height: 48%;
    border-radius: 32px 32px 0px 0px;
    position: relative;
}

.input_viewResult_calculator{
    color: #fff;
    border: none;
    background: none;
    font-size: 50px;
    max-width: 280px;
    padding: 0px 15px;
    cursor: none;

    position: absolute;
    bottom: 10px;
    left: 0;
    text-align: right;
    overflow-x: hidden;
}
.menu_calculator{
    height: 62%;
    display: flex;
    flex-wrap: wrap;

    border-radius: 0px 0px 32px 32px;
    padding-left: 16px;
    margin-bottom: 10px;

    .calcBlockGrey{
        background-color: #a5a5a5;
        color: black;
        font-size: 20px;
    }

    .calcBlockOrange{
        background-color: #ff9e0b;
        font-size: 26px;

    }
}

.calcBlock{
    border-radius: 50%;
    margin-right: 16px;
    width: 50px;
    height: 50px;
    background-color: #333333;
    font-size: 24px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover{
        opacity: 0.7;
    }
}
.calcDoubleBlocK{
    width: 116px;
    height: 50px;
    border-radius: 35px;
    justify-content: left;
    padding-left: 18px;
}
