.header{
    height: 100vh;
    display: flex;
    align-items: center;
}

.header_block{
    width: 100%;
    height: 200px;
    text-align: center;
    opacity: 0;
    transition: all 2s;
    transform: scale(90%);

    .subtitle-medium{
        margin-top: 15px;
        display: none;
    }
}

.header_block-view{
    opacity: 1 ;
    transform: scale(100%) ;
}
.subtitle-medium-view{
    display: block !important;
}











