.notebookPhone{
    width: 265px;
    height: 565px;
    top: 17px;
    left: 17px;
    position: absolute;
    background-color: #f6f2a7;
    border-radius: 33px;
    transition: all 0.4s;
    z-index: 100;
    

    &:before{
        content: "";
        position: absolute;
        width: 154px;
        height: 50px;
        display: inline;
        top: 0;
        left: 56px;
        background-image: url(./../img/png/headerPhone.png);
        background-repeat: no-repeat;
    }
    
    .subtitle-medium{
        margin-top: 24px;
        text-align: center;

    }
}

.add_notebook{
    position: absolute;
    bottom: 15px;
    right: 15px;
    fill: #53c45b;
    width: 40px;
    height: 40px;

    &:hover{
        fill: #8fd693;
    }
}

.list_notebook{
    display: flex;
    flex-direction: column;
    margin-top: 10px;

    height: 490px;
    border-radius: 0px 0px 37px 32px;
    overflow-y: scroll;
}

.list_notebook::-webkit-scrollbar {
    width: 0;
}
.oneNote{
    display: flex;
    background-color: #faf7c7e1;
    border: 1px solid #faf7c7e1;

    padding: 15px 5px;
    margin-bottom: 5px;

    &:hover{
        border: 1px solid #ccdde2;
    }
}
.name_oneNote{
    margin-left: 5px;
    padding-left: 5px;
    overflow-x: hidden;
}

.create_notebook{
    width: 100%;
    height: calc(100% - 48px);
    border-radius: 0px 0px 25px 25px;
    background-color: #f0eff4;
}
.title_create_notebook{
    border: none;
    background-color: #fff;
    width: 100%;
    padding: 5px 15px;
    border-radius: 30px;
    margin-top: 10px;
    font-size: 16px;
    outline: none;
    cursor: none;

}   
.text_create_notebook{
    border: none;
    background-color: #f0eff4;
    padding: 5px;
    width: 100%;
    height: calc(100% - 105px);
    resize: none;
    outline: none;
    cursor: none;
    margin-top: 5px;
}

.text_create_notebook::-webkit-scrollbar {
    width: 0;
}

.create_btn_notebook{
    position: absolute;
    bottom: 15px;
    left: 15px;
    fill: #53c45b;
    width: 40px;
    height: 40px;

    &:hover{
        fill: #8fd693;
    }
}
.clear_btn_notebook{
    position: absolute;
    bottom: 15px;
    right: 15px;
    fill: #f27070;
    width: 40px;
    height: 40px;

    &:hover{
        fill: #f24444;
    }
}
.edit_btn_notebook{
    position: absolute;
    bottom: 15px;
    left: 15px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 10px;
    background-color: rgb(247, 186, 74);

    &:hover{
        background-color: rgb(250, 168, 15);
    }
}