@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;500;700;900&display=swap');

body {
    font-size: 14px;
    font-family: 'Montserrat', Arial, sans-serif;
    background-color: rgb(248, 248, 248);
}

.container{
    max-width: 1560px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
}

.opacityShow {
    opacity: 1 !important;
}
// Всего font-weight: 100, 300, 500, 700, 900

// Жирные титульники
.title-large{
    font-weight: 900;
    font-size: 56px;
}
.title-medium{
    font-weight: 700;
    font-size: 42px;
}
.title-small{
    font-weight: 700;
    font-size: 20px;
}
// Титульники обычные и описания

.subtitle-large{
    font-weight: 500;
    font-size: 24px;
}
.subtitle-medium{
    font-weight: 300;
    font-size: 20px;
}
.subtitle-premedium{
    font-weight: 300;
    font-size: 14px;
}
.subtitle-small{
    font-weight: 100;
    font-size: 16px;
}

.subtitle-maxsmall{
    font-weight: 500;
    font-size: 8px;
}

// Индивидуальные настройки блоков
.margin-r0{
    margin-right: 0px !important;
}
.none{
    display: none;
}
.border-none{
    border: none !important;
}
.fontw500 {
    font-weight: 500;
}
.fontw700{
    font-weight: 700;
}



// MEDIA media MEDIA media MEDIA media MEDIA media MEDIA media MEDIA media 
// media запросы

@media (max-width: 430px){
    .title-large{
        font-size: 36px;
    }
    .title-medium{
        font-size: 32px;
    }
    .title-small{
        font-size: 14px;
    }
}
@media (max-width: 390px){
    .title-large{
        font-size: 28px;
    }
    .title-medium{
        font-size: 26px;
    }
    .title-small{
        font-size: 10px;
    }
}