.clockPhone{
    width: 265px;
    height: 565px;
    top: 17px;
    left: 17px;
    position: absolute;
    background-color: #f9f9f9;
    border-radius: 33px;
    transition: all 0.4s;
    z-index: 100;
    

    &:before{
        content: "";
        position: absolute;
        width: 154px;
        height: 50px;
        display: inline;
        top: 0;
        left: 56px;
        background-image: url(./../img/png/headerPhone.png);
        background-repeat: no-repeat;
    }
    
    .subtitle-medium{
        margin-top: 24px;
        text-align: center;
    }
}



.block_clockPhone{
    height: calc(100% - 250px);
    display: flex;
    align-items: center;
    justify-content: center;
}
.input_clockPhone{
    font-size: 40px;
    font-weight: 500;

}