.appPhone{
    margin-top: 300px;
    margin-bottom: 150px;
    opacity: 0;
    transition: opacity $transition-opacity-block-time;
    .title-medium{
        margin-bottom: 15px;
    }
   
}
.appPhone_block{
    // background-color: #fff;
    padding: 60px 20px;
    margin-top: 35px;

    border-radius: 10px;
   
    // -webkit-box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
    // -moz-box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
    // box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
}

.phone {
    width: 299px;
    height: 600px;
    margin: auto;
    position: relative;
    overflow: hidden;
    

    &::before{
        content: "";
        display: inline-block;
        width: 300px;
        height: 600px;
        top: 0;
        left: 0;
        position: absolute;

        background-image: url('./../img/iphone.png');
        background-repeat: no-repeat;
    }
}

#mobileCursor {
    width: 26px;
    height: 26px;
    position: absolute;
    display: none;
    z-index: 100;
    pointer-events: none;

    background-color: rgb(219, 219, 219);
    border-radius: 50%;
    opacity: 0.7;
}
.showPhone{
    width: 265px;
    height: 572px;
    top: 10px;
    left: 18px;
    position: absolute;
    background-color: #80808096;
    border-radius: 30px;
    display: flex;
    align-items: center;
    transition: all 0.4s;

}
.showPhone--loading{
    background-color: black;
}
.buttonShowPhone{
    margin: 0 auto;
    display: block;
    padding: 20px 35px;
    font-size: 16px;
    transition: all 0.3s;

    &:hover{
        background-color: rgba(0, 0, 0, 0.178);
    }
}
.buttonShowPhone--inactive{
    transform: scale(0%);
}

.appleLogo{
    width: 50px;
    height: 50px;
    margin: auto;
    display: none;
    opacity: 0;
    transition: all 2s;
}
.appleLogo--active{
    display: block;
}