.appProjectsMobile{
    display: none;
}
.projectsMobile{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin-top: 50px;
}

.oneProjectMobile{
    width: 100%;
    height: 700px;
    display: flex;
    margin-bottom: 75px;
}
.blockImg_oneProjectMobile{
    width: 70%;
    display: flex;
    justify-content: center;
}
.img_oneProjectMobile{
    max-width: 100%;
    max-height: 100%;
}
.info_oneProjectMobile{
    width: 30%;
    margin: 0 auto;
    text-align: center;
}

.text_info_oneProjectMobile{
    margin-top: 150px;

    .subtitle-medium{
        margin-bottom: 20px;
    }
}
.skills_info_oneProjectMobile{
    font-size: 16px;
    padding: 25px;
    border: 1px solid black;
    border-radius: 10px;
}












// MEDIA media MEDIA media MEDIA media MEDIA media MEDIA media MEDIA media 
// media запросы

@media (max-width: 1630px){
    .appProjectsMobile{
        display: block;
    }
}
@media (max-width: 1150px){
    .oneProjectMobile{
        height: 0%;
        flex-direction: column;
        align-items: center;
    }
    .blockImg_oneProjectMobile{
        width: 100%;
        max-width: 900px;
    }
    .text_info_oneProjectMobile{
        margin-top: 20px;
    }
    .info_oneProjectMobile{
        margin: 15px 0;
        width: 100%;
    }
}

