.appShifrator{

}
.appDescription{
    margin: 25px;
}
.appShifratorBlock{

}
.codeBlock{
    margin: auto;
    width: 300px;
    height: 450px;
    border: 1px solid #ccdde2;
    background-color: #fcfcfc;
    border-radius: 5px;
    padding: 15px;

}
.btnsCode{
    margin-bottom: 25px;
}
.btnCodeChange{
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 10px;
    border: none;
    transition: all 0.2s;

    &:hover {
        background-color: rgb(142, 213, 142);
        color: #fff;
    }
}
.btnCodeChange--active{
    background-color: rgb(142, 213, 142);
    color: #fff;
}

.entryCode{
}  

.entryBlockCode{
    display: flex;
    flex-direction: column;

    input {
        display: block;
        margin-bottom: 15px;
    }

    h3 {
        text-align: center;
        padding: 5px;
    }
}

.inputHardCode{
    margin-bottom: 0 !important; 
    cursor: pointer;
}
.inputMessageCode{
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #ccdde2;
    transition: all 0.2s;


    &:focus{
        outline: none;
        border: 1px solid rgb(142, 213, 142);
    }
}
.buttonEntryCode{
    margin-top: 25px;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid rgb(142, 213, 142);

    &:hover{
        background-color: rgb(142, 213, 142);
        color: #fff;
    }

}
.loadingShifr{
    margin-top: 50px;

}
.imgLoadingShifr{

    width: 100px;
    height: 100px;
    margin: auto;
}
.exitCode{
    margin-top: 15px;
}

.exitBlockCode{

}
.partExitCode{
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}
.exitKey{
    display: flex;
    align-items: center;
    border: 1px solid #ccdde2;
    height: 30px;
    padding: 5px;
    justify-content: space-between;
    border-radius: 5px;
}
.exitKeyOne{
    border: none;
    width: 100%;
}
.svgCopy{
    width: 25px;
    height: 25px;   
    padding: 0px 0px 0px 5px;
    cursor: pointer;
    opacity: 0.5;

    &:hover{
        opacity: 1;
    }
}
.exitKeyTwo{
    border: none;
    width: 100%;
}
.deMessage{
    width: 100%;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #ccdde2;

    max-width: 268px;
    max-height: 150px;
    min-width: 268px;
    min-height: 30px;
}

.inputWarning {
    border: 1px solid #fc6c6c;
}