.settingsPhone {
    width: 265px;
    height: 565px;
    top: 17px;
    left: 17px;
    position: absolute;
    background-color: #f0eff4;
    border-radius: 33px;
    transition: all 0.4s;
    z-index: 100;

    &:before{
        content: "";
        position: absolute;
        width: 154px;
        height: 50px;
        display: inline;
        top: 0;
        left: 56px;
        background-image: url(./../img/png/headerPhone.png);
        background-repeat: no-repeat;
    }
    
    .subtitle-medium{
        margin-top: 24px;
        text-align: center;

    }
}
.listSettings{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 40px;
    left: 0px;
    width: 100%;
    margin-top: 15px;
    overflow-y: scroll;
    max-height: 515px;
    border-radius: 0px 0px 32px 32px;
}
.listSettings::-webkit-scrollbar {
    width: 0;
}
.oneSetting{
    width: 100%;
    background-color: #fff;
    border-bottom: 1px solid #efeef1;
    padding: 0px 5px;

    display: flex;
    align-items: center;

    span{
        padding: 15px 0px;
        margin-left: 7px;
    }


   
}
.oneSetting--empty{
    background-color: #f0eff4;
    padding: 20px 0px;
}
.icon_oneSetting{
    width: 27px;
    height: 27px;
    padding: 5px;
    border-radius: 5px;
}