.appStore{
    margin-bottom: 150px;
    opacity: 0;
    transition: opacity $transition-opacity-block-time;
}
.appStore_block{

    .title-medium{
        margin-bottom: 25px;
    }


}
.bigBlock_appStore{
    width: 350px;
    height: 350px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 200px;
    border: 1px solid rgba(0, 0, 0, 0.05);
    border-radius: 20px;

    &:hover {
        border-color:rgba(0, 0, 0, 0.1);
    }

}
.miniBlock_appStore{
    width: 50%;
    height: 50%;
    padding: 15px;
    position: relative;
    background-color: #fff;
    cursor: pointer;
    
    .subtitle-premedium{
        margin: 5px;
        position: absolute;
    }

    &:hover{
        background: none;
    }
}
.mb-r1{
    border-radius: 20px 0 0 0;
    .logo_miniBlock_appStore{
        bottom: 25px;
        right: 25px;
    }
    .subtitle-premedium{
        left: 25px;
    }
}
.mb-r2{
    border-radius: 0 20px 0 0;
    .logo_miniBlock_appStore{
        bottom: 25px;
        left: 25px;
    }
    .subtitle-premedium{
        right: 25px;
    }

}
.mb-r3{
    border-radius: 0 0 0 20px;
    .logo_miniBlock_appStore{
        top: 25px;
        right: 25px;
    }
    .subtitle-premedium{
        bottom: 25px;
        left: 25px;
    }
}
.mb-r4{
    border-radius: 0 0 20px 0;
    .logo_miniBlock_appStore{
        top: 25px;
        left: 25px;
    }
    .subtitle-premedium{
        right: 25px;
        bottom: 25px;
    }
}
.goOut-1{
    transition: all 1.5s;
    transform: translate(-1000px, -1000px);
}
.goOut-2{
    transition: all 1.5s;
    transform: translate(1000px, -1000px);
}
.goOut-3{
    transition: all 1.5s;
    transform: translate(-1000px, 1000px);
}
.goOut-4{
    transition: all 1.5s;
    transform: translate(1000px, 1000px);
}
.logo_miniBlock_appStore{
    width: 45px;
    height: 45px;
    position: absolute;
}
.prilojenie_appStore{
    display: flex;
    flex-direction: column;
    align-items: center;

}
.back_appStore{
    margin-bottom: 25px;
    display: flex;
}
.backImg_appStore{
    width: 35px;
    height: 35px;
    margin-right: 15px;
    cursor: pointer;

    &:hover{
        opacity: .8;
    }
}
.voprosImg_appStore{
    width: 35px;
    height: 35px;
    &:hover{
        opacity: .8;
    }
}

[data-text] {
    position: relative;
}
[data-text]::after {
    content: attr(data-text);
    position: absolute; 
    width: 400px;
    left: -150px;
    bottom: 10px; 
    background: #ffffff; 
    color: #000;
    box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.3);
    border-radius: 5px;
    pointer-events: none;
    padding: 10px;
    opacity: 0; 
    transition: 0.5s; 
    text-align: center;
} 

[data-text]:hover::after {
    opacity: 1;
    bottom: 35px;
}