.block_1_skillsMore{
    margin-top: 100px;
    .skills {
        position: absolute;
        left: 300px;
        top: 125px;
        
        opacity: 0;
        transition: all $transition-line-time;
    }
}

.lineHor_1sm--250w{
    width: 0px;
    height: 1px;
    background-color: rgb(123, 123, 123);
    transition: all $transition-line-time;
}
.lineHor_1sm--250w--active{
    width: 250px;
}
.lineHor_1sm--50w{
    width: 0px;
    height: 1px;
    background-color: rgb(123, 123, 123);
    position: absolute;
    bottom: 0;
    left: 250px;
    transition: all $transition-line-time;
}
.lineHor_1sm--50w--active{
    width: 50px;
}
.lineVert_1sm--200h{
    width: 1px;
    height: 0px;
    background-color: rgb(123, 123, 123);
    transition: all $transition-line-time;

}
.lineVert_1sm--200h--active{
    height: 200px;
}