.about_section{
    margin-bottom: 200px;
}
.about_block{
    opacity: 0;
    transition: opacity 3s;

    .title-medium {
        margin-bottom: 25px;
    }
}
.about{
    width: 100%;
    min-height: 500px;
    display: flex;
    overflow: hidden;

    border-radius: 10px;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
}
.info_about{
    width: 40%;
    padding: 20px;

    h1 {
        font-size: 22px;
        margin-top: 0px;
        margin-bottom: 5px;
    }
    p {
        font-size: 18px;
        line-height: 22px;
        margin-top: 10px;
    }
    .subtitle-premedium{
        margin: 120px 30px 20px 30px;
    }
}
.text_info_about{
    padding: 0px 10px;
    box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
    margin-top: 35px;
    border-radius: 20px;
    position: relative;
    cursor: pointer;

    &:hover{
        box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.3);
    }
    h3{
        padding: 25px 0;
        font-weight: 500;
        font-size: 14px;
        text-align: center;
    }
    p {
        font-size: 14px;
    }

    &::after{
        content: "";
        position: absolute;
        width: 14px;
        height: 8px;
        display: inline;
        top: 30px;
        right: 15px;
        opacity: 0.4;
        vertical-align: middle;

        background-image: url(../img/svg/close-open.svg);
        background-repeat: no-repeat;
    }
}
.text_info_about--active{
    &::after{
        transform: rotate(180deg);
    }
}
.selftext_info_about{
    padding: 20px 10px;
    p{
        font-size: 16px;
    }
}
.any_about{
    width: 60%;
    position: relative;
}
.foto_about{
    width: 100%;
    position: absolute;
    top: -30px;
    right: -30px;

    box-shadow: 0px 0px 50px -11px rgba(34, 60, 80, 0.2);
}












// MEDIA media MEDIA media MEDIA media MEDIA media MEDIA media MEDIA media 
// media запросы

@media (max-width: 1120px){
    .about{
        flex-direction: column;
    }
    .info_about{
        width: 100%;
    }
    .any_about{
        width: 100%;
    }
    .foto_about{
        position: static;
    }
}