.directoryPhone{
    width: 280px;
    height: 580px;
    top: -52px;
    left: -24px;
    position: absolute;
    background-color: rgba(108, 108, 108, 0.463);
    border-radius: 33px;
    transition: all 0.4s;
    z-index: 100;
    transform: scale(0%);
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .subtitle-medium{
        margin-bottom: 5px;
        color: #fff
    }
}
.block_directoryPhone{
    width: 220px;
    height: 150px;
    background-color: rgba(108, 108, 108, 0.823);
    border-radius: 15px;

}

.directoryPhone--active{
    transform: scale(100%);
}
.directoryPhone--inactive{
    transform: scale(0%);
}

.dir_listApps{
    display: flex;
    flex-wrap: wrap;
    margin-left: 12px;
    margin-top: 12px;
}
.dir_blockWithOneApp{
    border-radius: 10px;
    margin-right: 12px;
    margin-bottom: 15px;
    transition: all 0.2s;
    max-width: 40px;
    
    .subtitle-maxsmall{
        text-align: center;
        color: #fff;
        margin-top: 5px;
    }

    a {
        cursor: none;
    }

    &:hover{
       transform: scale(105%); 
    }
}
.icon_dirApp{
    width: 40px;
    height: 40px;
    border-radius: 20px;
}
.iconMini_dirApp{
    width: 9px;
    height: 9px;
    border-radius: 20px;
    margin-right: 1px;
}