.block_2_skillsMore{
    margin-top: 160px;

    .skills {
        position: absolute;
        left: -355px;
        top: 490px;
        
        opacity: 0;
        transition: all 1s;
    }
}
.lineHor_2sm--150w{
    width: 0px;
    height: 1px;
    background-color: rgb(123, 123, 123);
    
    transition: all $transition-line-time;
}
.lineHor_2sm--150w--active{
    width: 150px;
}
.lineVert_2sm--400h{
    width: 1px;
    height: 0px;
    background-color: rgb(123, 123, 123);

    position: absolute;
    left: 149px;

    transition: all $transition-line-time;
}
.lineVert_2sm--400h--active{
    height: 400px;
}
.lineHor_2sm--100w{
    width: 0px;
    height: 1px;
    background-color: rgb(123, 123, 123);

    
    position: absolute;
    top: 560px;
    left: 150px;

    transition: all $transition-line-time;
}
.lineHor_2sm--100w--active{
    transform: translateX(-100px);
    width: 100px;
}