}

.oneProject{
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    margin-top: 1530px;

    .subtitle-large{
        margin-bottom: 70px;
    }
    &:first-child{
        margin-top: 0;
    }
}
#project_1{

    .img_oneProject{
        background-color: #fff;
    }
    .info_oneProject{
        background-color: #fff;
    }
    .subtitle_oneProject{
        color: rgb(212, 212, 212);
    }
    .title_oneProject{
        color: #000;
    }
    .skills_text_oneProject{
        border: 1px solid #000;

        li {
            color: #000;
            &:before{
                border: 2px solid #000;
            }
        }
    }
    
   
