.img_oneProject{
    width: 60%;
    height: 100vh;

    position: absolute;
    top: 0;
    left: 0;
}
.img_img_oneProject{
    margin: auto;
    height: 100vh;

}
.info_oneProject{
    width: 40%;
    height: 2500px;

    padding: 50vh 0px;

    position: absolute;
    top: 0;
    right: 0;
}

.text_oneProject{
    height: 100vh;
    text-align: center;
    padding: 0 50px;
}

.subtitle_oneProject{
    font-size: 20px;
    
}
.title_oneProject{
    font-size: 56px;
    font-weight: 700;
    margin-top: 10px;
}

.skills_text_oneProject{
    font-size: 18px;
    text-align: left;

    padding: 30px;
    border-radius: 10px;


    li {
        position: relative;
        padding-left: 15px;
        margin-bottom: 5px;

        &:before{
            content: "";
            position: absolute;
            top: 7px;
            left: 0;
            min-width: 3px;
            min-height: 3px;
            // border: 2px solid #fff;
            border-radius: 50%;
        }
    }
}
.plus_oneProject{
    width: 40px;
    height: 40px;
    margin: 20px 0;
}



